.elementor-select-wrapper{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    color: inherit;
    font-size: inherit;
    font-family: inherit;
    font-weight: inherit;
    font-style: inherit;
    text-transform: inherit;
    letter-spacing: inherit;
    line-height: inherit;
    flex-basis: 100%;
    padding-right: 20px;
}
.elementor-field-textual{
    line-height: 1.4;
    font-size: 15px;
    width: 300px;
    margin-top: 10px;
    margin-bottom: 10px;
    min-height: 40px;
    padding: 5px 10px;
    border-radius: 3px;

}
label{
    text-align: left;
}
button{
    width: 300px;
}
.elementor-field-group .elementor-field-textual {
    /* width: 100%; */
    max-width: 100%;
    border: 1px solid #818a91;
    background-color: transparent;
    color: #373a3c;
    /* vertical-align: middle; */
    flex-grow: 1;
    background-color:#f5f5f5;
    border-color:  #7a7a7a;
}
.elementor-button{
    width: 290px;
    height: 33px;
    margin-left: 10px;
    background-color: gray;
    color: #fff;
}

